.navBarSection {
  width: 100%;
  height: -moz-max-content;
  height: max-content;
  position: fixed;
}
.navBarSection .header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 1rem;
  margin-left: 0.5rem;
  margin-right: 0.5rem;
}
.navBarSection .header .logoDiv {
  align-self: start;
}
.navBarSection .header .logoDiv .logo h1 {
  letter-spacing: 1px;
}
.navBarSection .header .logoDiv .logo .icon {
  width: 4rem;
  transform: translatey(35%);
  font-size: 2rem;
  margin-right: 0.5rem;
}
.navBarSection .header .navBar {
  align-self: flex-end;
}
.navBarSection .header .navLists {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  font-size: 1.2rem;
}
.navBarSection .header .navLists .navItem {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.navBarSection .header .navLists .closeNavbar {
  display: none;
}
.navBarSection .header .toggleNavbar {
  display: none;
}/*# sourceMappingURL=navbar.css.map */